﻿/// <reference path="../../main">


%themes {
    &.#{$prefix}theme {
        &-light, &-default {
            background: $secondary-light;
            color: $primary-dark;

            .btn {
                @extend %btn-primary;
            }
        }

        &-dark {
            background: $primary-medium;
            color: $primary-light;

            .btn {
                @extend %btn-secondary;
            }
        }
    }
}




