﻿/// <reference path="../../main">

.page-footer {
    background-color: $secondary-light;
    height: auto;
    width: 100%;
}

.footer-top {
    @include responsive-property('padding-left', $standard-padding-map);
    @include responsive-property('padding-right', $standard-padding-map);
    display: none;

    &-inner {
        padding-top: $gap;
        padding-bottom: $gap;
        max-width: 90rem;
        margin-left: auto;
        margin-right: auto;

        @include media-min(medium) {
            display: flex;

            .footer-block {
                flex-basis: 25%;
                flex-grow: 1;

                + .footer-block {
                    @include responsive-property('padding-left', $gutter-map);
                }
            }
        }

        @include media(medium) {

            .footer-block {
                + .footer-block {
                    @include responsive-property('padding-top', $gutter-map);
                }
            }
        }
    }
}

.footer-bottom {
    background-color: $primary-dark;
    @include font-size($small-font-sizes);
    @include responsive-property('padding-left', $standard-padding-map);
    @include responsive-property('padding-right', $standard-padding-map);

    .copyright {
        max-width: 90rem;
        margin-left: auto;
        margin-right: auto;
        color: $primary-light;
        padding-top: $gap-sm;
        padding-bottom: $gap-sm;
    }
}



