﻿/// <reference path="../../main">


// STRUCTURE
.#{$prefix}fiftyfifty {
    margin-left: auto;
    margin-right: auto;

    &-item {
        @include media-min(tablet) {
            flex: 1 1 auto;
            width: 50%;
        }
    }

    &-inner {
        @include media(tablet) {
            &.reversed {
                .#{$prefix}fiftyfifty-item:first-child {
                    @include hide;
                }
            }
        }

        @include media-min(tablet) {
            display: flex;

            &.reversed {
                .#{$prefix}fiftyfifty-item:last-child {
                    @include hide;
                }
            }
        }
    }
}

// BASIC STYLES
.#{$prefix}fiftyfifty {
    &-heading {
        @extend %visually-hidden;
    }

    &-item {
        display: flex;

        .#{$prefix}wysiwyg-block {
            @extend %standard-padding-h;
            @extend %standard-padding-v;
            width: 100%;
            display: flex;
            align-items: center;
        }
    }

    .#{$prefix}image-block {
        width: 100%; //IE fix
        figcaption {
            @extend %visually-hidden;
        }
    }
}

// HANDLING IMAGES AND ASPECT RATIO
.#{$prefix}fiftyfifty {

    .#{$prefix}block {
        width: 100%;
    }

    .#{$prefix}image-block, .#{$prefix}video-block {
        width: 100%;
        height: 0;
        position: relative;
        overflow: hidden;

        & > a {
            width: 100%;
            height: 100%;
        }

        img {
            @extend %full-bleed-image;
        }

        [data-full-bleed-image-ie] {
            @extend %full-bleed-image-ie-replacement;
        }
    }

    &-inner.ratio-standard {
        .#{$prefix}image-block, .#{$prefix}video-block {
            padding-bottom: ratioToPercent(4, 3);
        }
    }

    &-inner.ratio-wide {
        .#{$prefix}image-block, .#{$prefix}video-block {
            padding-bottom: ratioToPercent(16, 9);
        }
    }
}


// THEMES
.#{$prefix}fiftyfifty-item {
    @extend %themes;
}


