﻿.adage-block {
    &:not(.adage-block-full-bleed) {
        padding-bottom: $gap;
        max-width: 90rem;
        margin-left: auto;
        margin-right: auto;
    }
}


/* styling of form elements */
.EPiServerForms {
    .FormStep {

        > .Form__Element {
            display: flex;
            flex-direction: column;
        }
    }

    .FormChoice {
        span.Form__Element__Caption {
            @include font-size($medium-font-sizes);
            font-weight: $font-weight-bold;
            color: $primary-medium;
            margin-bottom: $gap-sm;
            margin-top: $gap-sm;
            padding-top: $gap-sm;
            border-top: $primary-medium 1px solid;
        }

        label {
            font-weight: $font-weight-normal;
        }
    }

    .FormParagraphText {
        h2 {
            margin-top: $gap-sm;
            padding: $gap-sm;
            background: $secondary-light;
            position: fixed;
            bottom: 0;
            width: 100%;
            left: 0;
            text-align: center;
            z-index: 999;

            &.error-message {
                color: $red;
                background: lighten($red, 35%);
            }
        }
    }

    .Form__NavigationBar {
        button.Form__NavigationBar__Action {
            max-width: unset !important;
            @extend %btn-primary;
            background-image: unset !important;
            width: unset !important;
            height: unset !important;
        }
    }
}

.adage-fwcta {
    &-content {
        a {
            color: #fff;
        }
    }
}

.rich-text {
    h2 {
        margin-top: $gap;
        padding: $gap-sm;
        background: $secondary-light;
        text-align: center;
    }

    h3 {
        color: $primary-medium;
    }

    h5 {
        @include font-size($small-font-sizes);
        font-weight: $font-weight-bold;
        margin-bottom: $gap-sm;
        color: $secondary-medium;
    }

    p + h5 {
        margin-top: $gap-sm;
        padding-top: $gap-sm;
        border-top: $secondary-medium 1px solid;
    }
}

body {
    padding-top: 100px;

    main > .adage-block-full-bleed {
        margin-top: 0;
    }
}


