﻿/// <reference path="../../main">


// STRUCTURE
.#{$prefix}accordion {
    max-width: $max-line-length;

    &-panel {
        overflow: hidden;
        transition: $transition height;

        &[aria-hidden="true"] {
            height: 0;
        }
    }
}


// STYLES
.#{$prefix}accordion {
    margin-left: auto;
    margin-right: auto;

    &-heading {
        margin-bottom: $gap-sm;
    }

    &-trigger {
        @extend %un-button;
        @include font-size($medium-large-font-sizes);
        display: block;
        width: 100%;
        padding-top: $space;
        padding-bottom: $space;
    }

    &-item {
        border-top: 1px solid $secondary-light;

        &:last-child {
            border-bottom: 1px solid $secondary-light;
        }
    }

    &-panel {
        &-content {
            padding-top: $space;
            padding-bottom: $space;
        }
    }
}


// ARROW/INDICATOR
.#{$prefix}accordion {
    &-trigger {
        position: relative;
        padding-right: $ui-icon-size * 3;

        &:after {
            content: '';
            width: $ui-icon-size;
            height: $ui-icon-size;
            display: block;
            position: absolute;
            right: $gap-sm;
            top: 50%;
            transform: translateY(#{-(hypotenuse($ui-icon-size, $ui-icon-size) / 2)}) rotate(45deg);
            transition: $transition transform;
            border-color: $primary-medium;
            border-style: solid;
            border-width: 0 $ui-icon-thickness $ui-icon-thickness 0;
        }

        &[aria-expanded="true"] {
            &:after {
                transform: translateY(#{-(hypotenuse($ui-icon-size, $ui-icon-size) / 4)}) rotate(-135deg);
            }
        }
    }
}



