﻿.survery-results-block {
    @include font-size($small-font-sizes);

    ul {

        &.survery-results-block-header {
            margin-top: 2rem;
            background: $primary-medium;
            color: #fcfcfc;
            @include font-size($small-font-sizes);
            border: none;
            text-transform: uppercase;

            li, span {
                border: none;
            }
        }

        li {
            border-bottom: 1px solid $primary-dark;
            border-left: 1px solid $primary-dark;
            border-right: 1px solid $primary-dark;
            display: flex;

            span {
                padding: 0.5rem;
                display: block;
                flex-grow: 1;

                + span {
                    border-left: 1px solid $primary-dark;
                    width: 8rem;
                    max-width: 8rem;
                }
            }

            &:nth-child(even) {
                background: #fcfcfc;
            }
        }
    }
}



